  strong {
    color: rgb(174, 87, 124);
    display: inline-block;
    background: rgba(132, 143, 224, 0.1);
    border-radius: 5px;
    padding: 0 2px;
  }
  &.am {
    color: rgb(174, 87, 124);
  }
}
